
.SightingDiv{
    position: absolute;
  
    border: 4px solid #ff1493;
    border-radius: 1rem;
  
}

.SightingDiv:hover  .VoteButtonDiv {
    display: block;
}

.VoteButtonDiv{
    padding:5px;
    background-color: rgba(200, 200, 200, 0.4);
    position: absolute;
    margin: 0 auto;
    left: 5px;
    bottom: 5px;
    display: none;
}

.VoteButtonResultDiv{
    padding:5px;
    background-color: rgba(200, 200, 200, 0.4);
    position: absolute;
    margin: 0 auto;
    left: 5px;
    bottom: 5px;
   
}


.VoteButtonLabel{
    font-size: 0.9em;
    font-weight: bold;
}

.VoteButton{
    float: left;
    font-size: 0.8em;
    border: 1px solid #ccc;
}
@media(max-width: 768px) {
    .VoteButtonLabel{
        font-size: 0.7em;
      
    }
    
    .VoteButton{
        float: left;
        font-size: 0.6em;
        border: 1px solid #ccc;
    }
}